.button-modal-movie--close {
    width: 20px;
    height: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.button-modal-movie--close::after,
.button-modal-movie--close::before {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%) rotate(45deg);
    width: 18px;
    height: 2px;
    background-color: $accent-color;
    content: '';
}

.button-modal-movie--close::before {
    transform: translate(-50%, -50%) rotate(-45deg);
}

